<template>
    <div class="search-aira">
        <el-form :inline="true" :model="formData" class="demo-form-inline">
            <el-form-item label="用户名">
                <el-input v-model="formData.user" placeholder="请输入用户名" clearable />
            </el-form-item>
            <el-form-item label="岗位">
                <el-input v-model="formData.post" placeholder="请输入岗位" clearable />
            </el-form-item>
            <el-form-item label="组织">
                <el-input v-model="formData.organization" placeholder="请输入组织" clearable />
            </el-form-item>
            <el-form-item label="手机号">
                <el-input v-model="formData.tel" placeholder="请输入手机号" clearable />
            </el-form-item>
            <el-form-item>
                <el-input v-model="formData.content" placeholder="请输入要搜索的内容" clearable />
            </el-form-item>
            <el-form-item>
                <el-button size="large" @click="searchBut">搜索</el-button>
                <el-button type="primary" size="large" @click="addTableData">新增</el-button>
            </el-form-item>
        </el-form>
        <!-- 表格 -->
        <el-table
            :data="tableData"
            empty-text="没有数据"
            :header-cell-style="{
                background: '#eef1f6',
                color: '#3D3D3D',
                fontSize: '12px',
                height: '44px'
            }"
        >
            <el-table-column prop="user" label="用户名" width="180" align="center" />
            <el-table-column prop="post" label="所属组织" width="180" align="center" />
            <el-table-column prop="organization" label="上级" align="center" />
            <el-table-column prop="content" label="岗位" align="center" />
            <el-table-column prop="tel" label="手机号" align="center" />
            <el-table-column label="操作" align="center">
                <template #default="{ $index, row }">
                    <div class="edit-but">
                        <span @click="editBut($index, row)" style="color: #ff2d2d"> 编辑 </span>
                        <span
                            @click="editPermissions($index, row)"
                            style="color: #ff2d2d; margin: 0 48px"
                        >
                            权限操作
                        </span>
                        <el-switch
                            v-model="value2"
                            style="--el-switch-on-color: #dedede; --el-switch-off-color: #ff3f2f"
                            @click="offOn(row)"
                        />
                    </div>
                </template>
            </el-table-column>
        </el-table>
        <!-- 分页 -->
        <div class="pagination-contr">
            <el-pagination
                v-model:current-page="paginator.currentPage"
                v-model:page-size="paginator.pageSize"
                :page-sizes="[16, 26, 36, 46]"
                layout="->,total, sizes, prev, pager, next, jumper"
                :total="paginator.total"
                @size-change="handleSizeChange"
                @current-change="handleCurrentChange"
            />
        </div>
    </div>
    <!-- 新增编辑弹窗 -->
    <dialog-edit-box
        :row-data="subFormData"
        :loading="loading"
        :title="dialogTitile"
        :structure="structure"
        @cance-edit="canceEdit"
        @save-edit="saveEdit"
    ></dialog-edit-box>
</template>

<script lang="ts" setup>
import http from '@/util/http'
import { onMounted, reactive, ref } from 'vue'
import DialogEditBox from '@/components/DialogEditBox/index.vue'
import type { formDataType, tableDataType } from './index.ts'

// 搜索数据
const formData = reactive<formDataType>({
    user: '',
    post: '',
    organization: '',
    tel: '',
    content: ''
})
// 查询内容
const search = () => {
    http({
        url: 'https://mock.apifox.com/m1/2117465-3078121-default/request',
        method: 'POST',
        data: {
            currentPage: paginator.currentPage,
            pageSize: paginator.pageSize,
            ...formData
        }
    }).then(({ data }) => {
        // 设置表格数据
        tableData = Object.assign(tableData, data.dadaList)
        // 设置分页大小
        paginator.total = Number(data.total)
        paginator.currentPage = data.currentPage
        paginator.pageSize = data.pageSize
    })
}

//

// 表格数据
let tableData = reactive<tableDataType[]>([])

let loading = ref(false)

// 子组件数据
let subFormData = ref<tableDataType>({})

// 弹窗结构
let structure = ref([
    {
        label: '用户名',
        prop: 'user',
        type: 'input',
        placeholder: '请输入用户名',
        style: ''
    },
    {
        label: '岗位',
        prop: 'post',
        type: 'input',
        placeholder: '请输入岗位'
    },
    {
        label: '组织',
        prop: 'organization',
        type: 'input',
        placeholder: '请输入组织'
    },
    {
        label: '手机号',
        prop: 'tel',
        type: 'input',
        placeholder: '请输入手机号'
    }
])

// 编辑按钮
const editBut = (index: number, row: any): void => {
    // 设置弹窗标题
    dialogTitile.value = '编辑'
    // 打开弹窗
    loading.value = true
    // 设置子数据
    subFormData.value = row
}
// 权限按钮
const editPermissions = (index: number, row: any): void => {
    console.log('Edit Permissions')
}
// 开关按钮
const offOn = (row: any): void => {
    console.log('按钮关闭')
}

// 开关
const value2 = ref(true)

// 分页
const paginator = reactive({
    currentPage: 1,
    pageSize: 16,
    total: 0
})

const handleSizeChange = (val: number) => {
    console.log(`${val} items per page`)
}

// 查询按钮
const searchBut = () => {
    paginator.currentPage = 1
    search()
}

// 新增数据
const addTableData = () => {
    dialogTitile.value = '新增'
    subFormData.value = {}
    loading.value = true
}

// 保存数据
const saveEdit = (parent: any) => {
    alert('发送网络请求：' + JSON.stringify(parent))
}

// 指定页查询
const handleCurrentChange = (currentPage: number) => {
    paginator.currentPage = currentPage
    search()
}

// 初始化数据
search()

// 弹窗标题
const dialogTitile = ref<'新增' | '编辑'>('新增')

// 弹窗关闭
const canceEdit = (show: boolean) => {
    loading.value = show
}
</script>

<style lang="scss" scoped>
.search-aira {
    box-sizing: border-box;
    margin: 20px;
    .demo-form-inline .el-input {
        --el-input-width: 220px;
    }

    .demo-form-inline .el-select {
        --el-select-width: 220px;
    }
    .el-table {
        // 修改表格移入移出背景色
        :deep(.el-table__body tr:hover) {
            background-color: #ffedec;
            color: #ff321f;
        }
        :deep(.el-table__body tr:hover > td.el-table__cell) {
            background-color: #ffedec;
        }
        .edit-but {
            color: #160907;
        }
    }
}
.pagination-contr {
    padding: 10px;
}
</style>
